<template>
  <!-- 
    @mousewheel.prevent
    
  -->
  <div id="home" @mousewheel.prevent>
    <!--  -->
    <NavBar class="home-nav">
      <template v-slot:center>
        <div></div>
      </template>
    </NavBar>
      <!-- - -->
      <TapControl class="tab-control tabfixed" ref="tabControlview" 
                  :titles="titles" @tabClick="tabClick"
                  v-show="isTabFixed"
      ></TapControl>
    <!--  -->
    <Scroll class="content" ref="scroll" 
      :probeType="3" @scroll="scrolling"
      :pullUpLoad="true" @pullingUp="LoadMore"
    >
      <!--  -->
      <Swipe :banners="banners" @swipeLoad="swipeLoad"></Swipe>
      <!--  -->
      <RecommendView :recommends="recommends"></RecommendView>
      <!--  -->
      <FeatureView></FeatureView>
      <!--  -->
      <TapControl class="tab-control" ref="tabControl" 
                  :titles="titles" @tabClick="tabClick"
      ></TapControl>
      <!--  -->
      <GoodList :goods="showGoods"></GoodList>
    </Scroll>
    <BackToTop v-show="isShow" class="back-to-top" @click.native="backClick"></BackToTop>
  </div>
</template>
<script>
  // 
  import NavBar from 'components/common/navbar/NavBar';
  import TapControl from 'components/content/tapControl/TapControl';
  import Scroll from 'components/common/scroll/Scroll';
  
  // 
  import Swipe from 'views/home/childComps/Swipe';
  import RecommendView from 'views/home/childComps/RecommendView';
  import FeatureView from 'views/home/childComps/FeatureView';
  import GoodList from '@/components/content/goods/GoodList';
  import BackToTop from '@/components/content/backTop/BackToTop';

  // 
  import {getHomeMultidate, getHomeGoods} from '@/network/home.js';
  import BScroll from 'better-scroll';
  import {debounce} from 'common/util';

  export default {
    name : 'home',
    components: {
      NavBar,
      Swipe,
      RecommendView,
      FeatureView,
      TapControl,
      GoodList,
      Scroll,
      BackToTop
    },
    data() {
      return {
        banners: [],
        recommends: [],
        titles: ['', '', ''],
        goods: {
          'pop' : {page: 0, list: []},
          'new' : {page: 0, list: []},
          'sell' : {page: 0, list: []},
        },
        currentType: 'pop',
        isShow: false,
        tabOffsetTop : 0,
        isTabFixed : false
      }
    },
    created() {
      // 1.
      this.getHomeMultidate()
      this.getHomeGoods('pop')
      this.getHomeGoods('new')
      this.getHomeGoods('sell')

    },
    mounted() {
      const refresh = debounce(this.$refs.scroll.refresh, 200)
      // item
      this.$bus.$on('itemImageLoad', () => {
        refresh()
      })

    },
    computed: {
      showGoods() {
        return this.goods[this.currentType].list
      },
    },
    methods: {
      /*  
       *    
       */
      tabClick(index) {
        this.$refs.tabControl.currentIndex = index
        this.$refs.tabControlview.currentIndex = index
        // keysindexcurrentType
        this.currentType = Object.keys(this.goods)[index]
      },
      backClick() {
        // ScrollscrollscrollTo
        this.$refs.scroll.scrollTo()
      },
      scrolling(position) {
        // 
        this.isShow = (-position.y) >= 1000

        // tabcontrol
        this.isTabFixed = (-position.y) >= this.tabOffsetTop
      },
      LoadMore() {
        this.getHomeGoods(this.currentType)
      },
      swipeLoad() {
        this.tabOffsetTop = this.$refs.tabControl.$el.offsetTop
      },
      /* 
       * 
       */
      getHomeMultidate() {
        getHomeMultidate().then(res => {
          this.banners = res.data.banner.list
          this.recommends = res.data.recommend.list
        })
      },
      getHomeGoods(type) {
        const page = this.goods[type].page + 1
        getHomeGoods(type, page).then(res => {
          this.goods[type].list.push(...res.data.list)
          this.goods[type].page = page

          this.$refs.scroll.finishPullUp()
        })
      },
    },
  }
</script>

<style scoped>
  #home{
    height: calc(100vh - 93px);
    position: relative;
  }

  .home-nav {
    background-color: var(--color-tint);
    color: white;

    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    z-index: 9;
  }
  
  .tab-control {
    z-index: 9;
  }
  
  .tabfixed {
    position: fixed;
    left: 0;
    right: 0;
    top: 44px;
  }

  .content {
    margin-top: 44px;
    height: calc(100vh - 93px);
    overflow: hidden;
  }
</style>